import React, { useState, useEffect } from "react";

import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Button } from "@material-ui/core";
import Badge from "@material-ui/core/Badge";
import Grid from "@material-ui/core/Grid";
import { Modal } from 'react-bootstrap';

import axios from 'axios'
import APIROUTE from '../Constants/ApiRoute'
import useAuth from 'stores/Author/AuthStore'
import 'bootstrap/dist/css/bootstrap.min.css';
import Modaljs from './components/Modal'
import CapNhatBanGhi from './Admin_CapNhatBanGhi';
import DuyetHoSo from './Admin_DuyetHoSo';

import LinearProgress from '@material-ui/core/LinearProgress';

import { API_HOST } from 'config/config'


const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});


const LuuTruXe = () => {
  const classes = useStyles();
  const [posts, setPosts] = useState([]);
  const [isRefresh, setIsRefresh] = useState(false);
  const [stateAuth,] = useAuth();
  const [openloading, setOpenloading] = useState(false);
  const [IDBanGhi, setIDBanGhi] = useState(0);

  const [modalUpdateShow, setmodalUpdateShow] = useState(false);
  const [modalDuyetHS, setmodalDuyetHS] = useState(false);

  useEffect(() => {
    (async () => {
      setOpenloading(true)
      await axios.get(`${API_HOST}${APIROUTE.RECORD_PROFILE}`, {
        headers: {
          Authorization: 'Bearer ' + stateAuth.accessToken
        }
      })
        .then(res => {
          setPosts(res.data);
        })
        .catch(err => console.log(err))
      setOpenloading(false)
    })()
     // eslint-disable-next-line
  }, [isRefresh])


  const onHandleDelete = async (IDBAnGhiXoa) => {
    await axios.delete(`${API_HOST}${APIROUTE.RECORD_PROFILE}/${IDBAnGhiXoa}`, {
      headers: {
        Authorization: 'Bearer ' + stateAuth.accessToken  // Gi token ln  xc nhn ngi dng.
      }
    })
  }
  return (
    <Grid container spacing={3}>
      {/* <Loading open={openloading} /> */}
      <TableContainer component={Paper}>
        {openloading ? <LinearProgress color="secondary" /> : null}
        <Table className={classes.table} aria-label="simple table">

          <TableHead>
            <TableRow>
              <TableCell align="center">ID bn ghi</TableCell>
              <TableCell align="center">Loi Xe</TableCell>
              <TableCell align="center">Bin S</TableCell>
              <TableCell align="center">Tn ch xe</TableCell>
              <TableCell align="center">Ngy To</TableCell>
              <TableCell align="center">Ngy Cp Nht</TableCell>
              <TableCell align="center">Trng thi</TableCell>
              <TableCell align="center">X l</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {posts.map((post) => (
              <TableRow key={post.id}>
                <TableCell align="center">
                  <Badge badgeContent={post.id} color="primary"></Badge>
                </TableCell>
                <TableCell align="center">{post.brand}</TableCell>
                <TableCell align="center">{post.numberPlate}</TableCell>
                <TableCell align="center">{post.firstName} {post.lastName}</TableCell>
                <TableCell align="center">{post.createDate} </TableCell>
                <TableCell align="center">{post.updatedDate}</TableCell>
                {post.isActive === true ? (
                  <TableCell align="center">
                    <Badge badgeContent='Approved' color="primary"></Badge>
                  </TableCell>
                ) : (
                    <TableCell align="center">
                      <Badge badgeContent='Unapproved' color="error"></Badge>
                    </TableCell>
                  )}
                {post.isActive === false ? (
                  <TableCell style={{ display: "inline-flex" }}>
                    <Button
                      variant="contained"
                      color="inherit"
                      className={classes.button}
                      onClick={
                        () => {
                          onHandleDelete(post.id)
                          setIsRefresh(!isRefresh);
                        }
                      }
                    ><span class="material-icons">
                        delete_forever
                  </span> </Button>

                    <Button
                      variant="contained"
                      color="default"
                      className={classes.button}
                      onClick={() => {
                        setIDBanGhi(post.id)
                        setmodalUpdateShow(true)
                      }}
                    >
                      <span class="material-icons">
                        build
                  </span>
                    </Button>

                    <Button
                      variant="contained"
                      color="primary"
                      className={classes.button}
                      onClick={() => {
                        setIDBanGhi(post.id)
                        setmodalDuyetHS(true)
                      }}
                    ><span class="material-icons">
                        done_outline
                </span></Button>

                  </TableCell>
                ) : null}

              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Modaljs
        IDBanGhi={IDBanGhi}
        header={"Cp nht h s"}
        show={modalUpdateShow}
        onHide={() => setmodalUpdateShow(false)}
      >
        <Modal.Body>
          <CapNhatBanGhi IDBanGhi={IDBanGhi} />
        </Modal.Body>
        <Modal.Footer>
          <Button >Hu</Button>
          <Button 
          // onClick={() => {
          //   axios.get(`${API_HOST}${APIROUTE.RECORD_PROFILE}${APIROUTE.APPROVAL}/${IDBanGhi}`, {
          //     headers: {
          //       Authorization: 'Bearer ' + stateAuth.accessToken  // Gi token ln  xc nhn ngi dng.
          //     }
          //   })
          //     .then(() => {
          //       setmodalDuyetHS(false)
          //       setIsRefresh(!isRefresh)
          //     })
          //     .then(err => console.log(err))
          // }}
          >Xc nhn cp nht</Button>
        </Modal.Footer>
      </Modaljs>

      <Modaljs
        IDBanGhi={IDBanGhi}
        header={"Vui lng kim tra li thng tin h s trc khi duyt"}
        show={modalDuyetHS}
        onHide={() => setmodalDuyetHS(false)}
      >
        <Modal.Body>
          <DuyetHoSo IDBanGhi={IDBanGhi} />
        </Modal.Body>
        <Modal.Footer>
          <Button>Khng Duyt</Button>
          <Button onClick={() => {
            axios.get(`${API_HOST}${APIROUTE.RECORD_PROFILE}${APIROUTE.APPROVAL}/${IDBanGhi}`, {
              headers: {
                Authorization: 'Bearer ' + stateAuth.accessToken  // Gi token ln  xc nhn ngi dng.
              }
            })
              .then(() => {
                setmodalDuyetHS(false)
                setIsRefresh(!isRefresh)
              })
              .then(err => console.log(err))
          }}>Duyt H S</Button>
        </Modal.Footer>
      </Modaljs>

    </Grid>
  );
};

export default LuuTruXe;
