import React, {useEffect} from 'react'
import axios from 'axios'
import APIROUTE from '../Constants/ApiRoute'
import { Modal } from 'react-bootstrap';
import { makeStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import { Button } from "@material-ui/core";
import UpdateProfile from './Admin_UpdateProfile';
import {API_HOST} from 'config/config'
import useAuth from 'stores/Author/AuthStore'




const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

const MyVerticallyCenteredModal = (props) => {
  console.log('props :>> ', props);
  return (
    <Modal
      show={props.isOpen}
      onHide={props.toggle}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
          <Modal.Title>Cp nht h s cng dn</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <UpdateProfile idUser = {props.id} />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={props.toggle}>
            Hy b
          </Button>
          <Button variant="primary" onClick={props.toggle}>
            Cp nht
          </Button>
        </Modal.Footer>

    </Modal>
  );
}

const Admin_quanlyuser = () => {
  //MODAL Update Profile

  const [userId, setUserId] = React.useState('');
  const [isOpen, setIsOpen] = React.useState(false);
  const [isRefresh, setIsRefresh] = React.useState(false);
  const [listUser, setListUser] = React.useState([]);
  const [stateAuth, ] = useAuth()

 const classes = useStyles();


const deleteUser = async (idUser) => {
  await axios.delete(`${APIROUTE.RECORD_PROFILE}/${idUser}`);
  setIsRefresh(!isRefresh)
}


useEffect(() => {
  (async () => {
    await axios.get(`${API_HOST}/citizenship-profiles`, {
      headers: {
        Authorization: 'Bearer ' + stateAuth.accessToken
      }
    })
      .then(res => {
       setListUser(res.data)
      })
      .catch(err => console.log(err))
  })()
   // eslint-disable-next-line
}, [isRefresh])


const editUser = (idUser) => {
  setIsOpen(true);
  setUserId(idUser)
}
const handleCloseModal = () => {
  setIsOpen(false)
  setUserId('');
}

  return (
    <>
    <LinearProgress color="secondary" />
    <MyVerticallyCenteredModal isOpen={isOpen} id={userId} toggle={handleCloseModal}/>
    <TableContainer component={Paper}>
    
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>ID</TableCell>
            <TableCell align="left">H tn</TableCell>
            <TableCell align="left">Ngy sinh</TableCell>
            <TableCell align="left">a ch</TableCell>
            <TableCell align="left">S in thoi</TableCell>
            <TableCell align="left">CMND</TableCell>
            <TableCell align="left">Qun l</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {listUser.map((user) => (
            <TableRow key={user.id}>
              <TableCell component="th" scope="row">
                {user.id}
              </TableCell>
              <TableCell align="left">{user.firstName} {user.lastName}</TableCell>
              <TableCell align="left">{user.dateOfBirth}</TableCell>
              <TableCell align="left">{user.addresses}</TableCell>
              <TableCell align="left">{user.phoneNumber}</TableCell>
              <TableCell align="left">{user.cmnd}</TableCell>
              <TableCell align="left">

                {/* Cap nhat ho so cong dan */}
              <Button
                    variant="contained"
                    color="primary"
                    className={classes.button}
                    onClick={() => editUser(user.id)}
                  >
                    <span class="material-icons">edit</span>
                  </Button>

                  {/* Xoa ho so cong dan */}
                  <Button
                    variant="contained"
                    color="secondary"
                    className={classes.button}
                    onClick={() => deleteUser(user.id)}
                  >
                    <span class="material-icons">
                    delete
                    </span>
                  </Button>

              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      
    </TableContainer>
    </>
  );

}

export default Admin_quanlyuser
