import { createStore, createHook } from "react-sweet-state";
import { toast } from "react-toastify";
import axios from "axios";
import {API_HOST} from 'config/config'

const initialState = {
  username: "",
  password: "",
  message: "",
  isLogin: false,  //sua cho nay thanh false
  accessToken: "",
  refreshToken:""
};

const actions = {
    login: (value) => async ({ setState }) => {
      const { username: user = "", password = "" } = value;
      const accessToken = localStorage.getItem('accessToken')
      if(accessToken){
        setState({
          refreshToken: localStorage.getItem('refreshToken'),
          accessToken: localStorage.getItem('accessToken')
          ,isLogin: true
        });
        return;
      }
      try {
        const response = await axios({
          method: 'post',
          url: `${API_HOST}/api/v1/auth/login`,
          data: {
            user,
            password
          }
        });
        if(response?.data?.accessToken){
          toast.success("Login Success");
          localStorage.setItem('refreshToken', response?.data?.refreshToken)
          localStorage.setItem('accessToken', response?.data?.accessToken)
          setState({
            username: response?.data?.username,
            message: "SUCCESS",
            isLogin: true,
            refreshToken: response?.data?.refreshToken || "",
            accessToken: response?.data?.accessToken || ""
          });
        } else {
          toast.error("Login Failed");
        }
      } catch (error) {
        toast.error("Login Failed");
      }
    },
    logout: () => ({ setState }) => {
      toast.success("Logout Success");
      setState({
        username: "",
        password: "",
        message: "",
        isLogin: false,
      });
      localStorage.removeItem('refreshToken')
      localStorage.removeItem('accessToken')
    },
    signup: (value) => async ({ setState }) => {
      console.log('value', value)
      try {
        const response = await axios({
          method: 'post',
          url: `${API_HOST}/api/v1/auth/register`,
          data: value
        });
        if(response?.data?.accessToken){
          toast.success("SignUp Success");
          setState({
            username: response?.data?.username,
            message: "SUCCESS",
            isLogin: true,
            refreshToken: response?.data?.refreshToken || "",
            accessToken: response?.data?.accessToken || ""
          });
        } else {
          toast.error("SignUp Failed");
        }
      } catch (error) {
        console.log({error})
        toast.error("SignUp Failed");
      }
    },
    checkLogin: () => ({ setState }) => {
      const accessToken = localStorage.getItem('accessToken')
      if(accessToken){
        setState({
          refreshToken: localStorage.getItem('refreshToken'),
          accessToken: localStorage.getItem('accessToken')
          ,isLogin: true
        });
        return;
      } 
    }
  },
  Store = createStore({
    initialState,
    actions,
  });

const useAuth = createHook(Store);

export default useAuth;
