import React, { useState, useEffect } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import useAuth from 'stores/Author/AuthStore';
import APIROUTE from '../Constants/ApiRoute';

import Loading from './Loading';

import axios from 'axios';
import { API_HOST } from 'config/config';

function Admin_DuyetHoSo({ IDBanGhi }) {

  const [post, setPost] = useState({});
  const [openloading, setOpenloading] = useState(true);
  const [stateAuth,] = useAuth();

  useEffect(() => {
    axios.get(`${API_HOST}${APIROUTE.RECORD_PROFILE}/${IDBanGhi}`, {
      headers: {
        Authorization: 'Bearer ' + stateAuth.accessToken
      }
    })
      .then(res => {
        console.log(res);
        setPost(res.data);
        setOpenloading(false);
      })
      .catch(err => console.log(err))
       // eslint-disable-next-line
  }, [])

  return (
    <div className="container">
      <Loading open={openloading} />
      <div className="row">
        <div className="col-12">
          <div className="input-group mb-2">
            <div className="input-group-prepend">
              <span className="input-group-text" id="basic-addon3">
                Ten chu xe
              </span>
            </div>
            <input
              disabled type="text"
              className="form-control"
              id="basic-url"
              aria-describedby="basic-addon3"
              value={post.firstName}
            />

          </div>
        </div>

      </div>
      <div className="row">
        <div className="col-12">
          <div className="input-group mb-2">
            <div className="input-group-prepend">
              <span className="input-group-text" id="basic-addon3">
                a ch
              </span>
            </div>
            <input
              disabled type="text"
              className="form-control"
              id="basic-url"
              aria-describedby="basic-addon3"
              value={post.addresses}
            />
          </div>
        </div>

      </div>
      <div className="row">
        <div className="col-12">
          <div className="input-group mb-2">
            <div className="input-group-prepend">
              <span className="input-group-text" id="basic-addon3">
                S CMND/H Chiu
              </span>
            </div>
            <input
              disabled type="text"
              className="form-control"
              id="basic-url"
              aria-describedby="basic-addon3"
              value={post.idNo}
            />
          </div>
        </div>


      </div>
      <div className="row">
        <div className="col-12">
          <div className="input-group mb-2">
            <div className="input-group-prepend">
              <span className="input-group-text" id="basic-addon3">
                S in Thoi
              </span>
            </div>
            <input
              disabled type="text"
              className="form-control"
              id="basic-url"
              aria-describedby="basic-addon3"
              value={post.phoneNumber}
            />
          </div>
        </div>


      </div>
      <div className="row">
        <div className="col-12">
          <div className="input-group mb-2">
            <div className="input-group-prepend">
              <span className="input-group-text" id="basic-addon3">
                Nhn Hiu
              </span>
            </div>
            <input
              disabled type="text"
              className="form-control"
              id="basic-url"
              aria-describedby="basic-addon3"
              value={post.brand}
            />
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-12">
          <div className="input-group mb-2">
            <div className="input-group-prepend">
              <span className="input-group-text" id="basic-addon3">
                S Khung
              </span>
            </div>
            <input
              disabled type="text"
              className="form-control"
              id="basic-url"
              aria-describedby="basic-addon3"
              value={post.frameNumber}
            />
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-12">
          <div className="input-group mb-2">
            <div className="input-group-prepend">
              <span className="input-group-text" id="basic-addon3">
                Nm Sn Xut
              </span>
            </div>
            <input
              disabled type="text"
              className="form-control"
              id="basic-url"
              aria-describedby="basic-addon3"
              value={post.productionDate}
            />
          </div>
        </div>
      </div>
      <div className="row">
      </div>
      <div className="row">
      </div>
      <div className="row">
        <div className="col-12">
          <div className="input-group mb-2">
            <div className="input-group-prepend">
              <span className="input-group-text" id="basic-addon3">
                Mu sn
              </span>
            </div>
            <input
              disabled type="text"
              className="form-control"
              id="basic-url"
              aria-describedby="basic-addon3"
              value={post.color}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Admin_DuyetHoSo
